<template>
    <div>
        <p>{{ name }}</p><br/>
        <button @click="updateName">Change Name</button>
    </div>
</template>

<script>
export default {
    data(){
        return{
            name: 'Coder',
        }
    },

    methods: {
        updateName(){
            this.name = 'Coder UPADTE';
        }
    }
    
}
</script>


